










































































































































































































































































































.fselect .dropdown-toggle {
  cursor: default;
  padding-left: 8px;
  padding-top: 0px;
  padding-bottom: 0px;
}
.fselect .select-tag.default-tag-style {
  display: inline-block;
  color: rgba(0, 0, 0, 0.6);
  background-color: #e4e4e4;
  border-radius: 14px;
  padding: 3px 12px;
  font-size: 13px;
  margin: 2px;
}
.fselect .select-body {
  padding: 2px 0px 2px 0px;
  height: auto;
}
.fselect .x-remove {
  font-size: 9px;
  min-height: inherit;
}
.fselect .x-remove:hover {
  color: red;
}
.fselect .placeholder-text {
  line-height: 2em;
}
.fselect .caret-position {
  position: absolute;
  top: 50%;
  right: 4px;
  line-height: 0.5em;
  font-size: 10px;
}
.fselect .dropdown-menu .search-field {
  padding: 0px 20px;
}
.fselect .scrollable-dropdown {
  height: auto;
  max-height: 15em;
  overflow-x: hidden;
  overflow-y: auto;
}
.fselect .form-control {
  padding-right: 16px !important;
}
.fselect .hovered {
  background-color: rgb(238, 238, 238);
  color: #000;
}



