  display: block;
}
.formation.formation-container {
  width: 100%;
  background: transparent;
}
.formation .form-group-autoheight > div {
  height: 100%;
  margin-bottom: 0px;
}
.formation .form-group-autoheight button {
  height: inherit;
}
.formation .required-decoration {
  color: #e51c23;
}
.formation .no-vertical-spacing {
  margin-top: 0px;
  margin-bottom: 0px;
  padding-top: 0px;
  padding-bottom: 0px;
}
.formation .hidden {
  display: none;
}



